@import "../base-components";

.gallery {
	overflow: hidden;
	&__inner {
		display: flex;
		flex-wrap: wrap;
		margin: -8px -8px;
	}
	&__item {
		flex: 0 0 50%;
		max-width: 50%;
		padding: 8px 8px;
		box-sizing: border-box;
		&__thumb {
			position: relative;
			padding-top: 100%;
			background: #5a7443;
			border: 1px solid #eef1eb;
			margin-bottom: 20px;
			img {
				position: absolute;
				top: 0px;
				left: 0px;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		&__link {
			display: block;
			text-align: center;
			color: #5a7443;
			text-transform: uppercase;
			font-size: 14px;
			font-family: Ubuntu Condensed;
			text-decoration: underline;
		}

		@include media-breakpoint-up(lg) {
			flex: 0 0 33.3333%;
			max-width: 33.3333%;
		}
	}
}
